<script>
import storageManager from '@/plugins/storage';
import Loader       from '@/interface/template/Loader.vue';
import Subhead from '@/interface/template/outline/Subhead.vue';
import Pagination from '@/utils/Paginated.vue';
import BatchProgress from '@/utils/BatchProgress.vue';
import convertUtils from "@/plugins/convertUtils";

export default {
    data() {
      const session = storageManager.get('session')
      const paging = storageManager.get('paging');
      return {
        hostkey:this.$Key,
        open: false,
        dataItem: this.$route.params.id,
        school: session.current.id,
        login:session.login,
        courseData: [],
        listItems: [],
        categoryData:[],
        loading_sources: true,
        endpoint: "",
        master:session.master,
        accessToken: storageManager.get('session','token'),
        configs: storageManager.get('configs'),
        session: storageManager.get('session'),

        limitOptions: [1,10, 50, 100, 200, 500, 1000, 2000, 3000, 4000], // Define the available options
        totalPages: 0,
        totalItems: 0,

        limitItem:50,
        currentPage: 1,
        searchQuery: "",
        debounceTimer: null,

        pagingInfo: paging || {
          [this.$route.path]: {
            limitItem: 50,
            currentPage: 1,
            searchQuery: "",
          },
        },

        loading: false,
        showingOverlay: false,
        statusFilter:"all",
        isCreatingUser: false,
        isShowingOverlay: false,
        loadingMessage: '',
        processingCount: '',
        activeBlock: false,
        scb:[],
        isProcessing: false,
        processName: "",
        progressText: "",
        progressTotal: 0,
        progressCurrent: 0,
        startTime: null,
        endTime: null,
        layoutVisibility: {
          order: true,
          enroll: true
        },
      }
    },
    components: {
      Loader,
      Subhead,
      Pagination,
      BatchProgress
    },
    methods: {
      togglePanel() {
        this.open = !this.open;
      },
      closePanel() {
        this.open = false;
      },
      leadingZero(number) {
        return number < 10 ? '0' + number : number.toString();
      },
      formatThaidate(date) {
        return convertUtils.toThaiDatetime(date,"short");
      },
      updateURLParameters() {
        const updatedUrlParams = new URLSearchParams(window.location.search);
        if (this.currentPage > 1) {
          updatedUrlParams.set('page', this.currentPage);
        } else {
          updatedUrlParams.delete('page');
        }
        if (this.searchQuery) {
          updatedUrlParams.set('searchQuery', this.searchQuery);
        } else {
          updatedUrlParams.delete('searchQuery');
        }
        const updatedUrl = `${window.location.pathname}?${updatedUrlParams.toString()}`;
        window.history.replaceState({}, '', updatedUrl);
      },
      handleLimitChange() {
        const paging = storageManager.get('paging') || {};
        paging[this.$route.path] = {
          limitItem: this.limitItem,
          currentPage: 1,
          searchQuery: this.searchQuery,
        };
        storageManager.set('paging', paging);
        this.updateURLParameters();
        this.getData();
      },
      handlePageChanged(page) {
        if (page !== this.currentPage) {
          window.scrollTo(0, 0);
          this.currentPage = page;
          const paging = storageManager.get('paging') || {};
          paging[this.$route.path] = {
            limitItem: this.limitItem,
            currentPage: this.currentPage,
            searchQuery: this.searchQuery,
          };
          storageManager.set('paging', paging);
          this.updateURLParameters();
          this.getData();
        }
      },
      clearSearchQuery() {
        this.searchQuery = '';
        const paging = storageManager.get('paging') || {};
        paging[this.$route.path] = {
          limitItem: this.limitItem,
          currentPage: this.currentPage,
          searchQuery: this.searchQuery,
        };
        storageManager.set('paging', paging);
        this.updateURLParameters();
        this.handleSearchKeydown({ key: 'Enter' });
        this.getData();
      },
      handleSearchKeydown(event) {
        clearTimeout(this.debounceTimer);
        const updateDataAndURL = () => {
          this.getData();
          const paging = storageManager.get('paging') || {};
          paging[this.$route.path] = {
            limitItem: this.limitItem,
            currentPage: 1, // Reset current page to 1
            searchQuery: this.searchQuery,
          };
          storageManager.set('paging', paging)
          this.updateURLParameters();
          this.handlePageChanged(1);
        };
        if (event.key === 'Enter' && this.searchQuery.length >= 3) {
          updateDataAndURL();
        } else {
          this.debounceTimer = setTimeout(updateDataAndURL, 500);
        }
      },
      toggleStatus(status) {
        console.log("Toggle Status", status);
        this.statusFilter = status;
        this.getData();
      },
      closeProgress() {
        this.isProcessing = false;
      },
      async batchProcess() {
        this.open         = false;
        let totalItems  = this.listItems.length;
        let counter       = 0;
        /* Batch Process Param */
        this.isProcessing   = true;
        this.processName    = `Render Billing`;
        this.progressTotal  = totalItems
        this.startTime      = new Date().toISOString();
        this.endTime        = null;
        for (const item of this.listItems) {
          counter++;
          /* Batch Process Param */
          this.progressText     = `Processing : ${counter}/${totalItems}`;
          this.progressCurrent  =  counter
          await this.executeCustomQueries(item.order.ref, item.order._id);
        }
        /* Batch Process Param */
        this.progressText = `All Process : Done`;
        this.endTime      = new Date().toISOString();
      },
      async getData() {
        this.listItems  = [];
        this.totalItems = 0;
        try {
          let andConditions = [{ parent: this.configs.siteID, role: 'superadmin' }];
          if (this.searchQuery) {
            andConditions.push({
              $or: [
                { name: { $regex: `.*${this.searchQuery}.*`, $options: 'i' } },
                { citizen: { $regex: `.*${this.searchQuery}.*`, $options: 'i' } },
                { email: { $regex: `.*${this.searchQuery}.*`, $options: 'i' } },
                { firstname: { $regex: `.*${this.searchQuery}.*`, $options: 'i' } },
                { lastname: { $regex: `.*${this.searchQuery}.*`, $options: 'i' } },
                { old_id: { $regex: `.*${this.searchQuery}.*`, $options: 'i' } },
                { phone: { $regex: `.*${this.searchQuery}.*`, $options: 'i' } },
                { token: { $regex: `.*${this.searchQuery}.*`, $options: 'i' } },
              ],
            });
          }
          const pipeline = [
            {
              $match: {
                $and: andConditions,
              },
            },
            {
              $facet: {
              table: [
                { $skip: (this.currentPage - 1) * this.limitItem },
                { $limit: this.limitItem },
                {
                  $project: {
                    user: "$$ROOT",
                  },
                },
              ],
              count: [
                { $count: "count" },
              ],
              },
            },
            ];

            const requestOptions = {
              method: 'POST',
              headers: { 'Content-Type': 'application/json','client-token-key':this.hostkey },
              body: JSON.stringify({
                  pipeline: pipeline,
              }),
            };

            const resAPI = await fetch('https://gateway.cloudrestfulapi.com/api/user/aggregate', requestOptions);
            const restReturn = await resAPI.json();

            if (restReturn && restReturn.length > 0) {
            const firstItem = restReturn[0];

            if (firstItem.table && firstItem.table.length > 0) {
              const table = firstItem.table;
              //console.log(firstItem.table);

              if (firstItem.count && firstItem.count.length > 0) {
                const count = firstItem.count[0].count;

                const formattedData = {
                  data: table,
                  total: count,
                  paging: {
                    page: this.currentPage,
                    limit: this.limitItem,
                    totalPages: Math.ceil(count / this.limitItem),
                  },
                };
                // Highlight search query in the result data
                this.listItems = formattedData.data.map((item) => {
                  console.log(item);
                  Object.keys(item).forEach((key) => {
                    if (typeof item[key] === "string") {
                      const lowerCaseValue = item[key].toLowerCase();
                      const searchQuery = this.searchQuery.toLowerCase();
                      const startIndex = lowerCaseValue.indexOf(searchQuery);

                      if (startIndex !== -1 && searchQuery.length > 0) {
                        const endIndex = startIndex + searchQuery.length;
                        const highlightedValue =
                          item[key].substring(0, startIndex) +
                          '<span class="text-red-500 font-bold">' +
                          item[key].substring(startIndex, endIndex) +
                          "</span>" +
                          item[key].substring(endIndex);
                          item[key] = highlightedValue;
                      }
                    }
                  });
                  return item;
                });
                this.totalItems   = formattedData.total;
                this.totalPages   = formattedData.paging.totalPages;
                this.loading      = false;
                this.activeBlock  = false;
                console.log("formattedData", this.formattedData);
              } else {
                console.error("Total count data is missing.");
              }
            } else {
              console.error("Paginated data is missing or empty.");
            }
          } else {
            console.error("No data in 'restReturn' or 'restReturn' is empty.");
          }
        } catch (error) {
          console.log(error);
        } finally {
          this.loading = false;
          this.activeBlock = false;
        }
      },
    },
    async mounted() {
  try {
    const paging = storageManager.get('paging') || {};
    const routePaging = paging[this.$route.path] || {};

    // Apply the paging information from 'paging' constant
    this.limitItem = routePaging.limitItem || 50;
    this.currentPage = routePaging.currentPage || 1;
    this.searchQuery = routePaging.searchQuery || '';

    // Update URL parameters based on the paging information
    this.updateURLParameters();

    await this.getData();
  } catch (error) {
    console.error(error);
  }
},
    setup() {
      //console.log("Setup");
    },
    computed: {
      filteredListItems() {
        if (this.selectedStatus === "All") {
          return this.listItems;
        } else {
          return this.listItems.filter(item => item.status === this.selectedStatus);
        }
      }
    }
};
</script>

<template>

  <div v-if="open" class="relative z-10" aria-labelledby="slide-over-title" role="dialog" aria-modal="true">
    <div class="fixed inset-0"></div>
    <div class="fixed inset-0 overflow-hidden">
      <div class="absolute inset-0 overflow-hidden">
        <div class="pointer-events-none fixed inset-y-0 right-0 flex max-w-full pl-10">
          <div class="pointer-events-auto w-screen max-w-md">
            <div class="flex h-full flex-col overflow-y-scroll bg-white py-6 shadow-xl">
              <div class="px-4 sm:px-6">
                <div class="flex items-start justify-between">
                  <h2 class="text-base font-semibold leading-6 text-gray-900" id="slide-over-title">Panel title</h2>
                  <div class="ml-3 flex h-7 items-center">
                    <button @click="closePanel" type="button" class="relative rounded-md bg-white text-gray-400 hover:text-gray-500 focus:outline-none focus:ring-2 focus:ring-indigo-500 focus:ring-offset-2">
                      <span class="absolute -inset-2.5"></span>
                      <span class="sr-only">Close panel</span>
                      <svg class="h-6 w-6" fill="none" viewBox="0 0 24 24" stroke-width="1.5" stroke="currentColor" aria-hidden="true">
                        <path stroke-linecap="round" stroke-linejoin="round" d="M6 18L18 6M6 6l12 12" />
                      </svg>
                    </button>
                  </div>
                </div>
              </div>
              <div class="relative mt-6 flex-1 px-4 sm:px-6">
                <ul class="space-y-4">
                  <!-- <li><button class="text-blue-600 hover:underline" @click="renderStatus">Render Status</button></li> -->
                  <li><button class="text-blue-600 hover:underline" @click="renderBilling">Render Billing</button></li> 
                  <li><button class="text-blue-600 hover:underline" @click="renderEnroll()">Render Enroll</button></li>
                  <li><button class="text-blue-600 hover:underline" @click="renderProfile">Render Profile</button></li>
                  <!-- <li><button class="text-blue-600 hover:underline" @click="renderProvince">Render Province</button></li> -->
                  <!-- <li><button class="text-blue-600 hover:underline" @click="renderEnrollFirebase">Render Firebase</button></li> -->
                </ul>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>

  <div v-if="!loading_sources" class="text-center"><Loader/></div>
    <batch-progress
      :processName="processName"
      :progressText="progressText"
      :progressTotal="progressTotal"
      :progressCurrent="progressCurrent"
      :isVisible="isProcessing"
      :startTime="startTime"
      :endTime="endTime"
      @close="closeProgress"
    />
    <div>

    <Subhead 
      :navigation="
      [
        {
          name: null,
          function: 'getData',
          style: 'refresh',
          class: 'default-btn',
          visible: true,
          type: 'button',
        },
        {
          name: 'ย้อนกลับ',
          link: '/',
          style: 'chevron-left',
          class: 'default-btn',
          visible: true,
          type: 'button',
        },
      ]"
      @getData="getData"
    />

    <div>
      <div v-if="isShowingOverlay" class="loading-overlay">
        <div class="loading-text">{{ loadingMessage }}</div>
        <div class="processing-count">{{ processingCount }}</div>
      </div>
    </div>
  
    <div class="flex-1 pb-8 bg-gray-100 pt-2 pb-5 border-t">
      <div class="mt-4">
        <div class="mx-auto max-w-7xl px-6 sm:px-6 lg:px-6">
          <div class="mt-5 bg-white shadow ring-1 ring-black ring-opacity-5 mb-5">

          <div class="bg-gray-100">
            <div class="p-2">
                <div class="flex justify-between flex-col md:flex-row">
        
                    <div class="flex flex-col md:flex-row mb-2 md:w-1/2 md:mr-2 md:mb-0">
                      <input id="search" type="text" class="px-4 py-2 rounded-md border-gray-300 bg-white focus:outline-none w-full md:w-48" placeholder="ค้นหา..."
                      v-model="searchQuery"
                      @keydown="handleSearchKeydown"
                      />
                      <div v-if="searchQuery.length > 0">
                          <div class="ml-1 text-left text-gray-500">
                            ผลการค้นหา <span class="text-black font-semibold">{{ totalItems }}</span> รายการ (s) 
                            <button type="button" class="mt-2 text-blue-500 hover:underline focus:outline-none"
                            v-if="searchQuery.length > 0"
                            @click="clearSearchQuery"
                            > Clear
                            </button>
                          </div>
                      </div>
                    </div>
        
                    <div class="flex flex-col mb-2 md:mb-0 md:flex-row items-center">
                        <select v-model="limitItem" @change="handleLimitChange" class="px-4 py-2 rounded-md border-gray-300 bg-white focus:outline-none w-full md:w-24 md:mr-2">
                          <option v-for="option in limitOptions" :key="option" :value="option">{{ option }}</option>
                        </select>
                        <Pagination
                          :current-page="currentPage"
                          :total-items="totalItems"
                          :total-pages="totalPages"
                          :limit-items="limitItem"
                          :data-loading="loading"
                          :display-mode="'nav'"
                          @page-changed="handlePageChanged"
                          class="mt-2 md:mt-0"
                        />
                    </div>
        
                </div>
            </div>
          </div>

          <div class="border-t bg-gray-50 border-gray-300">
            <div class="flex text-sm text-gray-900">
              <div class="w-6/12 md:w-2/12 py-2 pl-4 text-left">
                <label class="flex items-center cursor-pointer">
                  <input type="checkbox" v-model="layoutVisibility.order" class="mr-1 rounded-sm border-gray-400"/>
                  คำสั่งซื้อ
                </label>
              </div>
              <div class="w-6/12 md:w-2/12 py-2 pl-4 text-left">
                <label class="flex items-center cursor-pointer">
                  <input type="checkbox" v-model="layoutVisibility.enroll" class="mr-1 rounded-sm border-gray-400"/>
                  การเรียน
                </label>
              </div>
            </div>
          </div>

          <div class="border-b border-t bg-gray-200 border-gray-300">
            <div class="flex text-sm font-semibold text-gray-900">
              <div class="w-1/12 py-2 pl-2 text-center">#</div>
              <div class="w-full md:w-4/12 py-2 pl-4 text-left">ชื่อ-นามสกุล</div>
              <div class="hidden md:block w-2/12 py-2 pl-4 text-left">Citizen ID</div>
              <div class="hidden md:block w-4/12 py-2 pl-4 text-left">การติดต่อ</div>
              <div class="hidden md:block w-1/12 py-2 pl-4 text-left">หลักสูตร</div>
              <div class="hidden md:block w-2/12 py-2 pl-4 text-left">วันที่ลงทะเบียน</div>
              <div class="hidden md:block w-3/12 py-2 pl-4 text-left">เครื่องมือ</div>
            </div>
          </div>
        
          <div class="bg-white min-h-[50px] relative" :data-content="'กำลังติดต่อฐานข้อมูล กรุณารอสักครู่.....'" :class="[(activeBlock?'isblock' : 'isunblock')]">
            <template v-if="listItems && listItems.length > 0">

              <template v-for="(order, index) in listItems" :key="order._id">

                <div class="flex border-b border-gray-200 text-sm hover:bg-gray-50">

                  <div class="w-1/12 py-2 flex items-center justify-center bg-[#f2f2f2]">
                    <span class="text-gray-600 font-semibold p-1">{{ leadingZero((currentPage - 1) * limitItem + index + 1) }}</span>
                  </div>

                  <div class="w-4/12 py-2 pl-4">
                    <router-link :to="'/student/detail/'+order.user._id+'?back=/lesson/detail/'+this.dataItem + '/enroll'" class="text-gray-700 hover:text-indigo-600">
                      <div class="flex items-center">
                        <img
                          :src="order.user.avatar_img"
                          class="w-10 h-10 mr-2 rounded-sm bg-gray-200"
                          alt="Profile Image"
                        >
                        <div class="ml-2">
                          <span v-html="order.user.firstname + ' ' + order.user.lastname + ' (' + order.user.old_id + ')'"></span><br>
                          <span class="text-sm font-normal text-gray-500" v-html="order.user.token"></span>
                        </div>
                      </div>
                    </router-link>
                  </div>

                  <div class="w-2/12 py-2 pl-4 hidden md:block">
                    <font-awesome-icon :icon="['fas','id-card']" class="text-gray-500 mt-[3px] mr-1 text-sm"/> <span v-html="order.user.citizen"></span>
                  </div>

                  <div class="w-4/12 py-2 pl-4 pr-3 hidden md:block">
                    <font-awesome-icon :icon="['fas','phone']" class="text-gray-500 mt-[3px] mr-2 text-sm"/><span v-html="order.user.phone"></span><br>
                    <font-awesome-icon :icon="['fas','envelope']" class="text-gray-500 mt-[3px] mr-2 text-sm"/><span v-html="order.user.email"></span>
                  </div>

                  <div class="w-1/12 py-2 pl-4 pr-3 hidden md:block">
                    <span class="font-semibold text-blue-500">{{ order.user.enroll ? (order.user.enroll.length > 0 ? order.user.enroll.length : '0') : '0' }}</span>
                    <font-awesome-icon :icon="['fas','book']" class="text-gray-500 mt-[3px] ml-2 text-md"/>
                  </div>

                  <div class="w-2/12 py-2 pl-4 pr-3 hidden md:block">
                    {{ order.user.createAt }}
                  </div>

                  <div class="w-3/12 py-2 pl-4 pr-3 hidden md:block">
                    <button @click="$router.push('/lesson/edit/'+ order.user._id)" type="button" class="mr-2 inline-flex justify-center rounded-md border border-gray-300 bg-white px-1 py-1 text-xs font-medium text-gray-700 shadow-sm hover:bg-gray-50 focus:outline-none focus:ring-2 focus:ring-gray-900 focus:ring-offset-2">
                      <font-awesome-icon :icon="['fas','square-pen']" class="text-black mt-[3px] mr-2 text-md"/>
                      <span>แก้ไข</span>
                    </button>
                  </div>

                </div>

                <div class="md:hidden p-2 border-b border-gray-300 bg-blue-50">
                  <div class="w-full">
                    <button @click="$router.push('/lesson/edit/'+ order.user._id)" type="button" class="mr-2 inline-flex justify-center rounded-md border border-gray-300 bg-white px-2 py-2 text-sm font-medium text-gray-700 shadow-sm hover:bg-gray-50 focus:outline-none focus:ring-2 focus:ring-gray-900 focus:ring-offset-2">
                      <font-awesome-icon :icon="['fas','square-pen']" class="text-black mt-[3px] mr-2 text-md"/>
                      <span>แก้ไข</span>
                    </button>
                  </div>
                </div>

              </template>

            </template>

            <template v-else>
              <div class="text-center p-5">ไม่มีข้อมูล</div>
            </template>

          </div>

          <div class="p-2 text-center">
            <Pagination
              :current-page="currentPage"
              :total-items="totalItems"
              :total-pages="totalPages"
              :limit-items="limitItem"
              :data-loading="loading"
              :display-mode="'summary'"
              @page-changed="handlePageChanged"
            />
          </div>

        </div>

      </div>
    </div>
  </div>
</div>
</template>

<style>
  input:focus-visible {
    outline: none;
  }
</style>

<style>
